.card-header {
  border: none;
  color: rgb(251, 250, 252);
  background: rgb(81, 102, 194);
  background: linear-gradient(
    90deg,
    rgb(81, 102, 194) 33%,
    rgb(213, 217, 241) 100%
  );
}
.card {
  margin: auto;
  border: none;
  width: 90%;
  height: 420px;
  overflow: scroll;
}

.sear {
  background: -webkit-linear-gradient(rgb(81, 102, 194), rgb(229, 231, 243));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.searchBar {
  margin-top: 40px;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}
.form-control {
  width: 70%;
}
.searchBtn {
  border: none;
  color: rgb(81, 102, 194);
  font-size: 20pt;
  background: transparent;
}

.searchBtn:hover {
  color: rgb(128, 141, 201);
}

.list-item p {
  font-size: 16px;
}

.btn-container {
  color: white;

  padding-bottom: 10px;
}

@media (max-width: 767px) {
  .btn-container {
    margin-bottom: 15px;
  }
}

.imgThumbnail {
  height: 300px;
}
.imgThumbnailCol {
  flex: 4 1;
}
.authors {
  font-style: italic;
  flex: 1 1;
  color: rgb(53, 34, 0);
}

.bookTitle {
  color: rgb(81, 102, 194);
}

.title {
  flex: 1 1;
}

.subTitle {
  font-style: italic;
  color: rgb(81, 102, 194);
}

.description {
  flex: 7 1;
}

.book-list-item {
  display: flex;
  margin-bottom: 10px;
}

.cont {
  font-family: "Nunito Sans", sans-serif;
  color: rgb(124, 122, 122);
  bottom: 0;
  height: 80vh;
}

.navbar {
  font-size: 20pt;
  height: auto;
  background-color: rgb(81, 102, 194);
}

.navItem {
  padding: 20px;
}

.logo {
  height: 50px;
  border-radius: 50px;
  margin: 5px;
}

.footer {
  font-size: 10pt;
  text-align: center;
  margin: 0px;
  padding: 5px;
  background-color: rgb(229, 231, 243);
}

.wrapper {
  height: auto;
  background-color: rgb(229, 231, 243);
}

